import React from "react";
import styles from "./style.module.css";
import { Col, Row } from "react-grid-system";

function FormInfo() {
  return (
    <>
      <div className={styles.common_left_box}>
        <h2 className={styles.common_info_head_text}>Estimation details</h2>
        <div className={styles.form_info}>
          <div className={styles.head}>
            <h2 className={styles.head_text}>Shipping information</h2>
          </div>
          <div className={styles.form_information}>
            <Row style={{ margin: 0 }}>
              <Col xxxl={6} xxl={6} xl={6} lg={6} sm={12} md={6}>
                <div className={styles.form_info_card}>
                  <ion-icon name="location-outline"></ion-icon>
                  <div>
                    <p>From</p>
                    <h3>Tashkent, Uzbekistan</h3>
                  </div>
                </div>
              </Col>
              <Col xxxl={6} xxl={6} xl={6} lg={6} sm={12} md={6}>
                <div className={styles.form_info_card}>
                  <ion-icon name="location-outline"></ion-icon>
                  <div>
                    <p>To</p>
                    <h3>Samarkand, Uzbekistan</h3>
                  </div>
                </div>
              </Col>
            </Row>
          </div>
        </div>
        <div className={styles.form_info}>
          <div className={styles.head}>
            <h2 className={styles.head_text}>Vehicle Information</h2>
          </div>
          <div className={styles.form_information}>
            <Row style={{ margin: 0 }}>
              <Col xxxl={6} xxl={6} xl={6} lg={6} sm={12} md={6}>
                <div className={styles.form_info_card}>
                  <ion-icon name="car-outline"></ion-icon>
                  <div>
                    <p>Year</p>
                    <h3>2023</h3>
                  </div>
                </div>
              </Col>
              <Col xxxl={6} xxl={6} xl={6} lg={6} sm={12} md={6}>
                <div className={styles.form_info_card}>
                  <ion-icon name="car-outline"></ion-icon>
                  <div>
                    <p>Make</p>
                    <h3>Honda</h3>
                  </div>
                </div>
              </Col>
              <Col xxxl={6} xxl={6} xl={6} lg={6} sm={12} md={6}>
                <div className={styles.form_info_card}>
                  <ion-icon name="car-outline"></ion-icon>
                  <div>
                    <p>Model</p>
                    <h3>SP232</h3>
                  </div>
                </div>
              </Col>
              <Col xxxl={6} xxl={6} xl={6} lg={6} sm={12} md={6}>
                <div className={styles.form_info_card}>
                  <ion-icon name="car-outline"></ion-icon>
                  <div>
                    <p>Condition</p>
                    <h3>Excellent</h3>
                  </div>
                </div>
              </Col>
            </Row>
          </div>
        </div>
        <div className={styles.form_info}>
          <div className={styles.head}>
            <h2 className={styles.head_text}>Contact Information</h2>
          </div>
          <div className={styles.form_information}>
            <Row style={{ margin: 0 }}>
              <Col xxxl={6} xxl={6} xl={6} lg={6} sm={12} md={6}>
                <div className={styles.form_info_card}>
                  <ion-icon name="calendar-clear-outline"></ion-icon>
                  <div>
                    <p>Pick up date</p>
                    <h3>23 July, 2023</h3>
                  </div>
                </div>
              </Col>
              <Col xxxl={6} xxl={6} xl={6} lg={6} sm={12} md={6}>
                <div className={styles.form_info_card}>
                  <ion-icon name="person-outline"></ion-icon>
                  <div>
                    <p>Full name</p>
                    <h3>Sardor Shokirhujayev</h3>
                  </div>
                </div>
              </Col>
              <Col xxxl={6} xxl={6} xl={6} lg={6} sm={12} md={6}>
                <div className={styles.form_info_card}>
                  <ion-icon name="call-outline"></ion-icon>
                  <div>
                    <p>Phone number</p>
                    <h3>+998 98 765 43 21</h3>
                  </div>
                </div>
              </Col>
              <Col xxxl={6} xxl={6} xl={6} lg={6} sm={12} md={6}>
                <div className={styles.form_info_card}>
                  <ion-icon name="mail-outline"></ion-icon>
                  <div>
                    <p>Email</p>
                    <h3>thenof.uz@gmail.com</h3>
                  </div>
                </div>
              </Col>
            </Row>
          </div>
        </div>
      </div>
    </>
  );
}

export default FormInfo;
