import React from "react";
import { Splide, SplideSlide } from "@splidejs/react-splide";
import "@splidejs/react-splide/css";
import "@splidejs/react-splide/css/skyblue";
import "@splidejs/react-splide/css/sea-green";
import "@splidejs/react-splide/css/core";
import BlogCardImg from "../../../../assets/common/blog-card.png";
import styles from "./style.module.css";
import "./style.css";
import CommonCardNews from "../../../common/common-card-news";

function BlogMostPopular() {
  //Get Datas
  const Data = [1, 2, 3, 4, 5, 6, 7];
  return (
    <>
      <div className="blog_splide">
        <div className={styles.blog_splide_box}>
          <p className={styles.blog_splide_subtext}>Самые популярные статьи</p>
          <Splide
            aria-label="My Favorite Images"
            options={{
              pagination: false,
              arrows: false,
              perPage: 3,
              gap: 30,
              breakpoints: {
                825: {
                  perPage: 2,
                },
                555: {
                  perPage: 1,
                },
              },
            }}
          >
            {Data.map(() => (
              <SplideSlide>
                <CommonCardNews
                  card_img={BlogCardImg}
                  card_date={"11 Январь, 2023"}
                  card_time={"9min read"}
                  card_description={
                    "Lorem ipsum dolor sit amet consectetur. Risus ultricies velit nibh tortor elit in. Lectus eu senectus urna netus ante id aenean volutpat lacus. Pellentesque cum imperdiet mattis risus vel nunc vulputate. Tellus eu id tortor egestas amet mattis."
                  }
                />
              </SplideSlide>
            ))}
          </Splide>
        </div>
      </div>
    </>
  );
}

export default BlogMostPopular;
